export default {
  name: 'mt-text',
    methods: {
        handleClick(evt) {
            this.$emit('click', evt);
        }
    },
  props: {
      size: String,
      type: {
        type: String,
        default: 'default'
      },
      color: String
  },
    computed: {
        computeStyle: function() {
            let _style = '';
            let size = this.size;
            let color = this.color;
            if (size) {
                _style += `font-size: ${size};`;
            }

            if (color) {
                _style += `color: ${color};`;
            }

            return _style;
        }
    }
};



