export default {
  name: 'mt-image',
    methods: {
        handleClick(evt) {
            this.$emit('click', evt);
        }
    },
  props: {
      src: String,
      width: String,
      height: String
  },
    computed: {
        computeStyle: function() {
            let width = this.width;
            let height = this.height;
            let _style = '';
            if (width) {
                _style += 'width:' + width + ';';
            }
            if (height) {
                _style += 'height:' + height + ';';
            }
            return _style;
        }
    }
};



